#loading-container {
    width: 200px;
    height: 200px;
    position: absolute;
    margin: -100px 0px 0px -100px;
    left: 50%;
    top: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
#loading-rotater {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    animation: rotateBase 20s infinite linear;
}
#loading-svg {
    animation: loading 2s infinite ease-in-out, rotate 2s infinite ease-in-out;
    stroke-dasharray: 48 * 3.14159;
}

@keyframes loading {
    0% {
        stroke-dashoffset: 48 * 3.14159;
    }
    100% {
        stroke-dashoffset: -48 * 3.14159;
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes rotateBase {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

#loading-text {
    font-size: 20px;
}
